.swiper-container {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.swiper-wrapper,
.swiper-slide > .p-top__img {
  height: 100%;
  width: 100%;
}

@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}
.swiper-slide-active > div,
.swiper-slide-duplicate-active > div,
.swiper-slide-prev > div {
  animation: zoomUp 10s linear 0s 1 normal both;
}

.swiper-slide::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  max-width: 97.5rem;
  width: 100%;
  height: 100.5%;
  background-image: linear-gradient(270deg, transparent 0 70%, #fff 100%);
  z-index: 1;
}
@media screen and (max-width: 767px) {
  .swiper-slide::after {
    background-image: none;
  }
}

.p-tab {
  padding-top: 6.25rem;
  padding-bottom: 6.25rem;
}

.tab-wrap {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0;
}

.tab-wrap:after {
  content: "";
  width: 100%;
  height: 3px;
  display: block;
  order: -1;
}

.tab-label {
  color: White;
  background: LightGray;
  font-weight: bold;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);
  white-space: nowrap;
  text-align: center;
  padding: 10px 1em;
  order: -1;
  position: relative;
  z-index: 1;
  cursor: pointer;
  border-radius: 5px 5px 0 0;
}

.tab-label:not(:last-of-type) {
  margin-right: 5px;
}

.tab-content {
  width: 100%;
  height: 0;
  overflow: hidden;
  opacity: 0;
}

/* アクティブなタブ */
.tab-switch:checked + .tab-label {
  background: DeepSkyBlue;
}

.tab-switch:checked + .tab-label + .tab-content {
  height: auto;
  overflow: auto;
  padding: 15px;
  opacity: 1;
  transition: 0.5s opacity;
  border-top: #ccc 1px solid;
  margin-top: -0.1875rem;
}

/* ラジオボタン非表示 */
.tab-switch {
  display: none;
}

.p-tab__item {
  display: flex;
  align-items: center;
  margin: 0 auto;
  padding: 1.25rem;
  border-bottom: dashed 1px #ccc;
}

.p-tab__item:last-child {
  border-bottom: dashed 1px #ccc;
}

.p-tab-date {
  font-size: 1rem;
}

.p-tab__tag {
  border: solid 1px #ccc;
  margin-left: 0.9375rem;
  padding: 0.1875rem 0.625rem;
  font-size: 1.125rem;
}

.p-tab__place {
  font-size: 1rem;
  margin-left: 1.25rem;
}

.p-tab__link {
  width: 12.5rem;
  border: solid 1px #ccc;
  padding: 0.1875rem 0.625rem;
  text-align: center;
  margin-left: 1.5625rem;
}

@media screen and (max-width: 767px) {
  .u-desktop {
    display: none;
  }
}

.u-mobile {
  display: none;
}
@media screen and (max-width: 767px) {
  .u-mobile {
    display: block;
  }
}